import { windowArray } from "./array-utils";
import { toString, boolsToBytes, byteToBools, fromString } from "./byte-utils";
import { Grid } from "./grid";

export class Puzzle {
  static version = 1;
  width: number;
  height: number;
  crosses: boolean[];

  constructor(width: number, height: number, crosses: boolean[]) {
    this.width = width;
    this.height = height;
    this.crosses = crosses;
  }

  getHeight(): number {
    return this.height;
  }
  getWidth(): number {
    return this.width;
  }

  toGrid(): Grid<boolean> {
    return new Grid(windowArray(this.crosses, this.width));
  }

  toString(): string {
    let byteArray = [Puzzle.version, this.width, this.height];
    byteArray = byteArray.concat(boolsToBytes(this.crosses));

    return toString(new Uint8Array(byteArray));
  }

  static fromString(strPuzzle: string): Puzzle {
    // console.log(`Puzzle from String: ${strPuzzle}`);
    let byteArray = fromString(strPuzzle);
    if (byteArray.length < 4) throw new Error(`Puzzle string is too short`);
    let version = byteArray[0];
    // console.debug(`version of the input is: ${version}`);
    if (version !== 1) throw new Error(`Unsupported version: ${version}`);

    let width = byteArray[1];
    let height = byteArray[2];
    // console.debug(`version: ${version}, width: ${width}, height: ${height}`);

    let allBits = new Array<boolean>();
    for (let i = 3; i < byteArray.length; i++) {
      let bools = byteToBools(byteArray[i]);
      allBits = allBits.concat(bools);
    }

    var crosses = new Array<boolean>();
    const totalCrosses = width * height;
    // console.debug(`totalCrosses: ${totalCrosses}`);
    for (let i = 0; i < totalCrosses; i++) {
      crosses.push(allBits[i]);
    }
    // console.debug(`totalCrosses: ${totalCrosses} -> ${crosses.length}  ->  ${crosses}`);
    return new Puzzle(width, height, crosses);
  }
}
