import { useParams } from "react-router-dom";
import Board from "../../components/board/board";
import { allPuzzles } from "../../components/puzzles/puzzles";
import { Puzzle } from "../../common/puzzle";

interface Props {}

const puzzles = Object.values(allPuzzles);

const PuzzlePage: React.FC<Props> = (props: Props) => {
  const { puzzleId } = useParams();

  if (puzzleId === undefined || puzzleId.trim() === "" || isNaN(parseInt(puzzleId))) {
    throw new Error("puzzleId is undefined");
  }
  const puzzleIdNumber = parseInt(puzzleId);

  if (!puzzles[puzzleIdNumber - 1]) {
    return (
      <div>
        <br /> <br />
        <h2>Puzzle not found: {puzzleIdNumber}</h2>
      </div>
    );
  } else {
    const strPuzzle = puzzles[puzzleIdNumber - 1];
    let puzzle = Puzzle.fromString(strPuzzle as string);
    return (
      <div>
        <Board puzzleId={puzzleIdNumber} puzzle={puzzle} />
      </div>
    );
  }
};

export default PuzzlePage;
