import { Buffer } from "buffer";
import { windowArray } from "./array-utils";

export function boolsToByte(bools: boolean[]): number {
  let bits = bools.map((bit) => (bit === true ? 1 : 0));
  let byte = 0;
  for (let i = 0; i < bits.length; i++) {
    byte |= bits[i] << (7 - i);
  }
  return byte;
}
export function boolsToBytes(bools: boolean[]): number[] {
  let windowed = windowArray(bools, 8);
  let bytes: number[] = [];
  for (let i = 0; i < windowed.length; i++) {
    bytes = bytes.concat(boolsToByte(windowed[i]));
  }

  return bytes;
}

export function byteToBools(byte: number): boolean[] {
  let bits = [];
  for (let i = 7; i >= 0; i--) {
    bits.push((byte >> i) & 1);
  }
  return bits.map((bit) => (bit === 1 ? true : false));
}

export function bytesToBools(bytes: number[]): boolean[] {
  let bools: boolean[] = [];
  for (let i = 0; i < bytes.length; i++) {
    bools = bools.concat(byteToBools(bytes[i]));
  }
  return bools;
}

